/* @flow */

import styled from 'styled-components';

export const Title = styled.h2`
  color: #362c2a;
  margin-top: 20px;
`;

export const Para = styled.p`
  color: #362c2a;
  line-height: 1.4;
  margin: 5px;
`;

export const TextWrapper = styled.div`
  max-width: 640px;
  margin: 0 auto;
  overflow: hidden;
`;

export const Li = styled.li`
  color: #362c2a;
  line-height: 1.4;
  margin: 5px;
`;



