/* @flow */

import React from 'react';
import PropTypes from 'prop-types';
import history from '../../history';

function isLeftClickEvent(event) {
  return event.button === 0;
}

function isModifiedEvent(event) {
  return !!(event.metaKey || event.altKey || event.ctrlKey || event.shiftKey);
}


/**
 * Returns the domain as String or false if the url has no domain associated.
 * @param url
 * @returns (String|false)
 */
const getDomain = url => {
  if (!url) {
    return false;
  }
  const urlWithoutDomain = url.indexOf(`://`) === -1;
  if (urlWithoutDomain) {
    return false;
  }
  return url.replace(`http://`, ``).replace(`https://`, ``).split(`/`)[0];
};

const isExternal = ({targetUrl, currentUrl}) => {
  if (targetUrl && targetUrl.startsWith) {
    if (targetUrl.startsWith(`mailto:`)) {
      return true;
    }
    if (targetUrl.startsWith(`tel:`)) {
      return true;
    }
  }
  // If the targetUrl has no domain, then we can say is an internal url.
  if (getDomain(targetUrl) === false) {
    return false;
  }
  const differentDomain = getDomain(currentUrl) !== getDomain(targetUrl);
  if (differentDomain) {
    return true;
  }
  return false;
};

class Link extends React.Component {
  static propTypes = {
    href: PropTypes.string.isRequired,
    onClick: PropTypes.func,
  };

  static defaultProps = {
    onClick: null,
  };

  handleClick = (event: MouseEvent) => {
    if (this.props.onClick) {
      this.props.onClick(event);
    }

    if (isModifiedEvent(event) || !isLeftClickEvent(event)) {
      return;
    }

    if (event.defaultPrevented === true) {
      return;
    }

    event.preventDefault();
    history.push(this.props.href);
  };

  render() {
    const props = {...this.props};
    const {href} = this.props;
    if (isExternal({targetUrl: href, currentUrl: window.location.href})) {
      let target = `_blank`;
      if (href.startsWith(`mailto:`)) {
        target = void 0;
      }
      if (href.startsWith(`tel:`)) {
        target = void 0;
      }
      return <a href={href} {...props} target={target}/>;
    }

    // eslint-disable-next-line jsx-a11y/anchor-has-content
    return <a {...this.props} onClick={this.handleClick} />;
  }
}

export default Link;



